import React, { PureComponent, Suspense } from "react";
import Media from "./Media";
import { themeImpressionTracking } from "../../../../utils";
import { isTagShop, suiteTrack } from "../../../../actions/api";
import Products from "./Products";
import AuthorDetails from "./AuthorDetails";
import InViewWrapper from "../../Elements/InViewWrapper";


export default class Card extends PureComponent {
  constructor(props) {
    super(props)
    this.mediaHeight = React.createRef();
  }
  componentDidMount() {
    const { Post, itemIndex, onClickPopUpSlider } = this.props
    if (suiteTrack || isTagShop) themeImpressionTracking(this.mediaHeight)
    if (Post.isPopUp) {
      onClickPopUpSlider(itemIndex, Post)
    }
  }

  render() {
    const { itemIndex , ThemeStyle, CardStyle, Post, ProductSetting, sliderHeight, clickToShowPopUp, index } = this.props;
    const { style } = CardStyle
    const cardStyle = { backgroundColor: style.background, minHeight: sliderHeight };
    const isVideo = [3,5].includes(Post.type) ? true : false

    return <InViewWrapper>
            {({ ref, inView }) => (
            <div data-id={Post.id} id={`tb_rt_id-${Post.id}`} className="tb_rtp_post_wrapper"  onClick={clickToShowPopUp(itemIndex, Post)} style={{ padding: style.padding / 2, opacity: inView ? 1 : 0, transition: inView ? 'opacity .5s ease-in-out' : '' }} tb-product={ Post.isShopIcon ? 1 : Post.isHotspot ? 2 : 0} area-label={Post.content.text} role="article" ref={ref}>
              <div className={`tb_rtp_post_in tb_icon_animate ${!isTagShop ? 'tb_rtp_post_in_animate' : ''}`} style={cardStyle} role="button" aria-label="Popup" tabIndex={0} ref={this.mediaHeight}>
                  <Media Post={Post} CardStyle={CardStyle} ThemeStyle={ThemeStyle} index={index} inView={inView} />
                  {!isTagShop ?
                    <div className="tb_rtp_play_ico">
                      <div className="tb__icon tb-play"><div></div></div>
                    </div>
                    : null}
                  {Post.isProduct ?
                    <Products Post={Post} ProductSetting={ProductSetting} key={`product_slide${Post.id}`} />
                    :
                    <AuthorDetails Post={Post} CardStyle={CardStyle} ThemeStyle={ThemeStyle} ProductSetting={ProductSetting} inView={inView} />
                  }
              </div>
          </div>
          )}
        </InViewWrapper>
  }
}
